import styled from 'styled-components'

export const OutputStyled = styled.output`
  display: block;
  padding: 20px;
  font-size: 28px;
  text-align: right;
  user-select: none;
  border-bottom: 4px solid #bbbbbb;
  background-color: #f3f3f3;
`



